<script>
  import Button   from './Button.svelte'
  import Phone    from './Phone.svelte'
  import Head     from './Head.svelte'
  let tit   = 'OpenWhats';
  let ddi   = '';
  let phone = '';
  const abreZap = ()=>{
    if((ddi.trim()!='')&&(phone.trim()!='')){
      window.open(`https://api.whatsapp.com/send?phone=${ddi}${phone}`);       
    } 
  }
</script>

<svelte:head>
<title>{tit}</title>
</svelte:head>
<style>.box{width:75%;margin:0 auto;border:1px solid #000;background-color:#000;color:#fff;font-family:monospace}</style>
<main>
  <Head titulo={tit}/>
  <Phone bind:ddi={ddi} bind:phone={phone}/>
  <Button open={abreZap} />
  <div class="box">
  Já precisou mandar zap pra aquele contato que você não quer adicionar? Nunca soube como fazer? Já existe solução:

http://open-whats.surge.sh/

Selecione o código do país e preencha com o DDD + número, que o open-whats abre pra você.

Projeto open source do Paiva Thiago.
</div>
</main>
